/*--------------------------------------------------------------
  # Back to top button
  --------------------------------------------------------------*/

.back-to-top {
  position: fixed;
  visibility: hidden;
  opacity: 0;
  right: 15px;
  bottom: 15px;
  z-index: 996;
  background: #149ddd;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  transition: all 0.4s;
}

.back-to-top i {
  font-size: 28px;
  color: #fff;
  line-height: 0;
}

.back-to-top:hover {
  background: #2eafec;
  color: #fff;
}

.back-to-top.active {
  visibility: visible;
  opacity: 1;
}

/*--------------------------------------------------------------
  # Header
  --------------------------------------------------------------*/

#header {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  transition: all ease-in-out 0.5s;
  z-index: 9997;
  transition: all 0.5s;
  padding: 0 15px;
  background: #040b14;
  overflow-y: auto;
}

#header .profile img {
  margin: 15px auto;
  display: block;
  width: 120px;
  border: 8px solid #2c2f3f;
}

#header .profile h1 {
  font-size: 24px;
  margin: 0;
  padding: 0;
  font-weight: 600;
  text-align-last: center;
  font-family: "Poppins", sans-serif;
}

#header .profile h1 a,
  #header .profile h1 a:hover {
  color: #fff;
  text-decoration: none;
}

#header .profile .social-links a {
  font-size: 18px;
  display: inline-flex;
  justify-content: center;
  background: #212431;
  color: #fff;
  line-height: 1;
  padding: 8px 0;
  margin-right: 4px;
  border-radius: 50%;
  text-align: center;
  width: 36px;
  height: 36px;
  transition: 0.3s;
}

#header .profile .social-links a:hover {
  background: #149ddd;
  color: #fff;
  text-decoration: none;
}

#main {
  margin-left: 300px;
  height: 100vh;
}

#admin {
  margin-right: 200px;
}

.navbar {
  align-items: center;
  justify-content: center;
  display: flex;
  height: 80px;
  z-index: 999;
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  margin: 0;
  padding: 0;
  box-shadow: var(--box-shadow);
  background-color: #fff;
}

.navbar_container {
  align-items: center;
  justify-content: space-between;
  display: flex;
  height: 80px;
  z-index: 1;
  width: 80%;
  margin: 0 auto;
}

.logo_container {
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
  left: 180px;
}

.logo_link {
  text-decoration: none;
  align-items: center;
  justify-content: center;
  display: flex;
}

.logo {
  width: 60px;
  height: 60px;
}

.logo_showcase{
  width:80px;
  height:80px;
}

.navbar_toggle {
  display: none;
  transition: all 0.3s ease-in-out;
}

.navbar_menu {
  list-style: none;
  align-items: center;
  display: flex;
  margin: 0;
  padding: 0;
}

.menu_items {
  height: 80px;
}

.navbar_links {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 125px;
  height: 100%;
  text-decoration: none;
  color: #666;
  font-size: 20px;
  transition: 0.3s all ease;
  cursor: pointer;
  /* Ensure it shows a pointer cursor */
}

.navbar_links:hover {
  transition: 0.3s all ease;
  color: #e96b56;
}

.navbar_btn {
  padding: 0 1rem;
  align-items: center;
  justify-content: center;
  display: flex;
}

.button {
  align-items: center;
  justify-content: center;
  display: flex;
  text-decoration: none;
  border: 1px solid var(--bs-primary);
  border-radius: 4px;
  outline: none;
  transition: ease-out 0.3s;
  padding: 10px 20px;
  color: #666;
  position: relative;
  z-index: 1;
  font-size: 18px;
}

.button:hover {
  color: #fff;
  cursor: pointer;
}

.button:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--bs-primary);
}

.button1 {
  align-items: center;
  justify-content: center;
  display: flex;
  text-decoration: none;
  border: 1px solid var(--bs-danger);
  border-radius: 4px;
  outline: none;
  transition: ease-out 0.3s;
  padding: 10px 20px;
  color: #666;
  position: relative;
  z-index: 1;
  font-size: 18px;
}

.button1:hover {
  color: #fff;
  cursor: pointer;
}

.button1:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: "";
  background-color: var(--bs-danger);
}

@media screen and (max-width: 720px){
  .mobile-nav-toggle{
    right: 130px !important;
  }
}

@media screen and (max-width: 430px){
  .mobile-nav-toggle{
    right: 105px !important;
  }
}

@media screen and (max-width: 1256px){
  .logo_container{
    left: 0;
    margin-left: 20px;
  }
}

@media screen and (max-width: 960px) {
  .navbar_container {
    align-items: center;
    justify-content: space-between;
    display: flex;
    width: 100%;
    margin: 0;
    z-index: 1;
    height: 80px;
  }

  .navbar_menu {
    display: grid;
    grid-template-columns: auto;
    justify-content: center;
    top: -1000px;
    position: absolute;
    margin: 0;
    width: 100%;
    opacity: 1;
    transition: all 0.3s ease;
    z-index: -1;
  }

  .navbar_menu.activated {
    background-color: #fff;
    justify-content: center;
    z-index: 99;
    top: 100%;
    opacity: 1;
    transition: all 0.5s ease;
    height: auto;
  }

  .navbar_toggle {
    align-items: center;
    justify-content: center;
    display: block;
    margin-right: 25px;
    transition: 0.3s all ease-in-out;
    font-size: 18px;
    color: #666;
    cursor: pointer;
  }

  .menu_items {
    height: 100%;
  }

  .navbar_links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .navbar_btn {
    margin-bottom: 2rem;
  }

  .button {
    align-items: center;
    justify-content: center;
    display: flex;
    width: 80%;
    height: 80px;
    margin: 0;
  }
}

@media (max-width: 1199px) {
  #header {
    left: -300px;
  }

  #main {
    margin-left: 0;
  }

  #admin {
    margin-right: 0;
  }
}

/*--------------------------------------------------------------
  # Navigation Menu
  --------------------------------------------------------------*/

/* Desktop Navigation */

.nav-menu {
  padding: 30px 0 0 0;
}

.nav-menu * {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav-menu>ul>li {
  position: relative;
  white-space: nowrap;
}

.nav-menu a,
  .nav-menu a:focus {
  display: flex;
  align-items: center;
  color: #a8a9b4;
  padding: 12px 15px;
  margin-bottom: 8px;
  transition: 0.3s;
  font-size: 15px;
}

.nav-menu a i,
  .nav-menu a:focus i {
  font-size: 24px;
  padding-right: 8px;
  color: #6f7180;
}

.nav-menu a:hover,
  .nav-menu .active,
  .nav-menu .active:focus,
  .nav-menu li:hover>a {
  text-decoration: none;
  color: #fff;
}

.navbar a:hover,
  .navbar .active,
  .navbar .active:focus,
  .navbar li:hover>a {
  text-decoration: none;
  color: #fff;
}

.nav-menu a:hover i,
  .nav-menu .active i,
  .nav-menu .active:focus i,
  .nav-menu li:hover>a i {
  color: #149ddd;
}

.navbar a:hover i,
  .navbar .active i,
  .navbar .active:focus i,
  .navbar:hover>a i {
  color: #e96b56;
}

/* Mobile Navigation */

.mobile-nav-toggle {
  position: fixed;
  right: 230px;
  top: 25px;
  z-index: 9998;
  border: 0;
  font-size: 24px;
  transition: all 0.4s;
  outline: none !important;
  background-color: #149ddd;
  color: #fff;
  width: 40px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
  border-radius: 50px;
  cursor: pointer;
}

.mobile-nav-active {
  overflow: hidden;
}

.mobile-nav-active #header {
  left: 0;
}

